.SearchBar {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    width: calc(100% - 30px);
    max-width: 750px;
    height: 50px;
    padding: 0px 15px;
    margin-top: 15px;
    background-color: #FFFFFF;
}

.Search {
    width: 25px;
}

.Animate {
    -webkit-animation: y-rotation 1s infinite linear;
            animation: y-rotation 1s infinite linear;
}

@-webkit-keyframes y-rotation {
    0%   {-webkit-transform: rotateY(0deg);transform: rotateY(0deg)}
    100% {-webkit-transform: rotateY(360deg);transform: rotateY(360deg)}
}

@keyframes y-rotation {
    0%   {-webkit-transform: rotateY(0deg);transform: rotateY(0deg)}
    100% {-webkit-transform: rotateY(360deg);transform: rotateY(360deg)}
}

.SearchBar input {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: calc(100% - 40px);
    height: 30px;
    margin: 0;
    padding: 0;
    border: 0;
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    font-size: 18px;
}

.SearchBar input:focus {
    outline: none;
    -webkit-box-shadow: 0 2px 0#C50101;
            box-shadow: 0 2px 0#C50101;
}


